import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import style from './DepositCheckSuccess.module.css';
import Status from '../../../components/Status';
import { useAppSelector } from '../../../hooks/store';
import { currencyFormat } from '../../../utils/general';
import { useDrawer } from '../../../hooks/drawer';

function DepositCheckSuccess() {
  const { t } = useTranslation();
  const { close } = useDrawer();

  const paymentDetails = useAppSelector((store) => store.payment.paymentResponse, shallowEqual);
  // const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  return (
    <div className="page">
      <MiniBox>
        <Status
          status="success"
          title={t('check_success.title')}
          ctaText={t('check_success.ctaText')}
          onNext={() => {
            close();
          }}
        >
          <Box>
            <Text variant="regular" className={`${style.transfer_initiated} text-light`} pt="16px">{t('check_success.info')}</Text>
            <Box className={style.card_info}>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('check_success.amount')}</Text>
                <Text w="50%" variant="regular" textAlign="left">{currencyFormat(paymentDetails?.amount || '')}</Text>
              </HStack>
              <HStack className={style.card_info_item}>
                <Text w="50%" variant="regular" className="text-light" textAlign="left">{t('check_success.depositTo')}</Text>
                <Box w="50%">
                  <Text variant="regular" textAlign="left">{account?.label}</Text>
                </Box>
              </HStack>
            </Box>
          </Box>
        </Status>
      </MiniBox>
    </div>
  );
}

export default DepositCheckSuccess;
